import { createRouter,createWebHistory } from "vue-router";
// 引入组件
import LoGin from '../components/LoGin'

import StuLogin from '../components/Student/StuLogin'
import StuGrades from '../components/Student/StuGrades'
import StuPassword from '../components/Student/StuPassword'
import StuMain from '../components/Student/StuMain'

import TeaLogin from '../components/Teacher/TeaLogin'
import TeaGrades from '../components/Teacher/TeaGrades'
import TeaPassword from '../components/Teacher/TeaPassword'
import TeaStudent from '../components/Teacher/TeaStudent'
import TeaMain from '../components/Teacher/TeaMain'

import ManLogin from '../components/Manage/ManLogin'
import ManClass from '../components/Manage/ManClass'
import ManGrades from '../components/Manage/ManGrades'
import ManStudent from '../components/Manage/ManStudent'
import ManTeacher from '../components/Manage/ManTeacher'
import ManPassword from '../components/Manage/ManPassword'
import ManMain from '../components/Manage/ManMain'
// 创建一个路由器
// import axios from 'axios'
const router = createRouter({
    history:createWebHistory(),
    routes: [
        {
            path: "/login",
            component: LoGin,
            name: "login",

        },
        {
            path: "/stu-login",
            component: StuLogin,
            name: "stu-login",
            children:[
                {
                    path: "/stu-grades",
                    component:StuGrades,
                    name: "stu-grades",
                },

                {
                    path: "/stu-password",
                    component:StuPassword,
                    name: "stu-password",
                },
                {
                    path: "/stu-main",
                    component: StuMain,
                    name: "stu-main",

                }

            ]
        },

        {
            path: "/tea-login",
            component: TeaLogin,
            name: "tea-login",
            children:[
                {
                    path: "/tea-grades",
                    component:TeaGrades,
                    name: "tea-grades",
                },

                {
                    path: "/tea-password",
                    component:TeaPassword,
                    name: "tea-password",
                },
                {
                    path: "/tea-main",
                    component: TeaMain,
                    name: "tea-main",

                },
                {
                    path: "/tea-student",
                    component: TeaStudent,
                    name: "tea-student",

                }

            ]
        },


        {
            path: "/man-login",
            component: ManLogin,
            name: "man-login",
            children:[
                {
                    path: "/man-main",
                    component: ManMain,
                    name: "man-main",

                },
                {
                    path: "/man-class",
                    component: ManClass,
                    name: "man-class",

                },
                {
                    path: "/man-teacher",
                    component: ManTeacher,
                    name: "man-teacher",

                },
                {
                    path: "/man-student",
                    component: ManStudent,
                    name: "man-student",

                },
                {
                    path: "/man-password",
                    component: ManPassword,
                    name: "man-password",

                },
                {
                    path: "/man-grades",
                    component: ManGrades,
                    name: "man-grades",

                },
            ]
        },



    ]
})

export default router