<template>
  <el-input v-model="pageInfo.queryId"  style="width:250px ;height:40px" placeholder="请输入教师id（例：2010011)" clearable="true"/>
  <el-button  @click="getAll" style="height:38px" type="primary">
    <el-icon><Search/></el-icon>
  </el-button>

  <el-button type="success" @click="add" plain>添加</el-button>
  <br><br>
  <el-dialog v-model="dialogFormVisible" title="新增老师" width="30%">
    <el-form :model="Teacher">
      <el-form-item label="教师id" :label-width="formLabelWidth" >
        <el-input v-model="Teacher.t_id" placeholder="请输入教师id" autocomplete="off" />
      </el-form-item>

      <el-form-item label="教师名称" :label-width="formLabelWidth">
        <el-input v-model="Teacher.t_name" placeholder="请输入教师名称" autocomplete="off" />
      </el-form-item>

      <el-form-item label="教师性别" :label-width="formLabelWidth">
        <el-select v-model="Teacher.t_sex" placeholder="Please select">
          <el-option label="男" value="男" />
          <el-option label="女" value="女" />
        </el-select>
      </el-form-item>

      <el-form-item label="教师密码" :label-width="formLabelWidth">
        <el-input v-model="Teacher.t_password"  placeholder="请输入教师密码" autocomplete="off" />
      </el-form-item>

      <el-form-item label="教师电话" :label-width="formLabelWidth">
        <el-input v-model="Teacher.telephone"  placeholder="请输入教师电话" autocomplete="off" />
      </el-form-item>

      <el-form-item label="教师专业" :label-width="formLabelWidth">
        <el-input v-model="Teacher.t_department" placeholder="请输入教师专业" autocomplete="off" />
      </el-form-item>
    </el-form>
    <template #footer>
      <span class="dialog-footer">
        <el-button type="info" @click="cancel">取消</el-button>
        <el-button type="success" @click="handleAdd">确认</el-button>
      </span>
    </template>
  </el-dialog>

  <el-dialog v-model="dialogFormVisibleEdit" title="编辑老师信息" width="30%">
    <el-form :model="Teacher">
      <el-form-item label="教师id" :label-width="formLabelWidth">
        <el-input v-model="Teacher.t_id" placeholder="请输入教师id" autocomplete="off" disabled="true"/>
      </el-form-item>

      <el-form-item label="教师名称" :label-width="formLabelWidth">
        <el-input v-model="Teacher.t_name" placeholder="请输入教师名称" autocomplete="off" />
      </el-form-item>

      <el-form-item label="教师性别" :label-width="formLabelWidth">
        <el-select v-model="Teacher.t_sex" placeholder="Please select">
          <el-option label="男" value="男" />
          <el-option label="女" value="女" />
        </el-select>
      </el-form-item>

      <el-form-item label="教师密码" :label-width="formLabelWidth">
        <el-input v-model="Teacher.t_password"  placeholder="请输入教师密码" autocomplete="off" />
      </el-form-item>

      <el-form-item label="教师电话" :label-width="formLabelWidth">
        <el-input v-model="Teacher.telephone"  placeholder="请输入教师电话" autocomplete="off" />
      </el-form-item>

      <el-form-item label="教师专业" :label-width="formLabelWidth">
        <el-input v-model="Teacher.t_department" placeholder="请输入教师专业" autocomplete="off" />
      </el-form-item>
    </el-form>
    <template #footer>
      <span class="dialog-footer">
        <el-button type="info" @click="cancel">取消</el-button>
        <el-button type="success" @click="handleUpdate">确认</el-button>
      </span>
    </template>
  </el-dialog>

  <el-table :data="tableData" stripe style="width: 100%">
    <el-table-column prop="t_id" label="教师id"  align="center"/>
    <el-table-column prop="t_name" label="教师名称"  align="center" />
    <el-table-column prop="t_sex" label="性别" align="center"/>
    <el-table-column prop="t_password" label="密码" align="center"/>
    <el-table-column prop="t_department" label="专业名称" align="center" width="250px"/>
    <el-table-column prop="telephone" label="电话" align="center"/>
    <el-table-column label="操作" align="center">

      <template #default="scope">
        <el-button size="small" type="info" @click="handleEdit(scope.row)">编辑</el-button>
        <el-button size="small" type="danger" @click="handleDelete(scope.row)">删除</el-button>
      </template>

    </el-table-column >
  </el-table>

  <div style="text-align: -webkit-center">
  <el-pagination
      v-model:currentPage="pageInfo.currentPage"
      v-model:page-size="pageInfo.pageSize"
      :page-sizes="[6, 8, 10]"
      layout="total, sizes, prev, pager, next, jumper"
      :total="pageInfo.total"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
  />
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: "ManTeacher",
  data(){
    return{
      dialogFormVisible:false,
      dialogFormVisibleEdit:false,
      Teacher:{},
      formLabelWidth:'140px',
      tableData:[],

      pageInfo: {
        currentPage: 1,
        pageSize: 10,
        total:0,
        queryId:''
      },
    }
  },

  created(){
    this.getAll()
  },

  methods:{
    cancel(){
      this.dialogFormVisible=false
      this.dialogFormVisibleEdit=false
      this.$message.info("已取消当前操作")
    },

    getAll(){
      let param;
      param="?currentPage="+this.pageInfo.currentPage
      param +="&pageSize="+this.pageInfo.pageSize
      param +="&t_id="+this.pageInfo.queryId

      axios.get("/managers/Tea/"+param).then((res)=>
      {
        console.log(res)
        this.pageInfo.currentPage=res.data.data.current
        this.pageInfo.pageSize=res.data.data.size
        this.pageInfo.total=res.data.data.total

        this.tableData =res.data.data.records

      })
    },
    resetForm(){
      this.Teacher={}
    },
//弹出添加窗口
    add(){
      this.dialogFormVisible = true
      this.resetForm()
    },
//确认添加
    handleAdd(){
      if(this.Teacher.t_id==null||this.Teacher.t_name==null||this.Teacher.t_sex==null||this.Teacher.t_password==null
          ||this.Teacher.t_department==null || this.Teacher.telephone==null)
        this.$message.error("添加内容都不能为空哦!")
      else
      {axios.post("/managers/saveTea",this.Teacher).then(res=>{
            console.log(res)
            if(res.data.flag)
            { this.dialogFormVisible=false;
              this.$message.success("添加成功！")
            }
            else
              this.$message.error(res.data.msg)
          }
      ).finally(()=>{
        this.getAll()
      })
    }},

//弹出编辑窗口
    handleEdit(row){
      axios.get("/managers/getOneTea/?tid="+row.t_id).then((res)=>{
        if(res.data.flag){
          this.Teacher=res.data.data
          this.dialogFormVisibleEdit=true;
        }
        else{
          this.$message.error(res.data.msg);
        }
      }).finally(()=>{
        this.getAll();
      })
    },

//编辑后确认
    handleUpdate(){
      axios.put("/managers/updateTea",this.Teacher).then(res=>{
            console.log(res)
            if(res.data.flag) {
              this.$message.success("更新成功！")
              this.dialogFormVisibleEdit = false
            }
            else
              this.$message.error(res.data.msg)
          }
      ).finally(()=>{
        this.getAll()
      })
    },

    handleDelete(row) {
      this.$confirm('此操作将永久删除该文件, 是否继续?', '温馨提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        axios(
            {
              url:"/managers/deleteTea",
              method:"DELETE",
              data:{t_id:row.t_id}
            }
        ).then((res) => {
              if (res.data.flag)
                this.$message.success('删除成功!');
              else
                this.$message.error(res.data.msg);
            }
        ).finally(() => {
              this.getAll()
            }
        )
      }).catch(() => {
        this.$message.info('已取消删除');
      })
    },


    handleSizeChange(pageSize) {
      this.pageInfo.pageSize=pageSize
      this.getAll()
    },

    handleCurrentChange(currentPage) {
      this.pageInfo.currentPage=currentPage
      this.getAll()
    }

  }
}

</script>

<style scoped>
.el-pagination{
  width: max-content;
  margin-top:20px;
}
</style>