<template>
  <el-input v-model="pageInfo.CName" style="height:33px;width:180px"  placeholder="请输入课程名" clearable="true"/>

  <el-select v-model="value" style="width:180px"  placeholder="请选择查询范围">
    <el-option
        v-for="item in options"
        :key="item.value"
        :label="item.label"
        :value="item.value"
    />
  </el-select>

  <el-button  @click="getAll" style="height:33px" type="primary">
    <el-icon><Search/></el-icon>
  </el-button>

  <br><br>
    <el-table :data="tableData" style="width: 100%">
      <el-table-column prop=s_department v-if="this.newValue=='department'" label="查询集体"  align="center"/>
      <el-table-column prop=s_major v-if="this.newValue=='major'" label="查询集体"  align="center"/>
      <el-table-column prop=s_class v-if="this.newValue=='class'" label="查询集体"  align="center"/>
      <el-table-column prop=highestScore label="最高分"  align="center"/>
      <el-table-column prop=lowestScore label="最低分" align="center"/>
      <el-table-column prop=averageScore label="平均分" align="center"/>
      <el-table-column prop=failCount label="不及格人数" align="center"/>
    </el-table>


  <div style="text-align: -webkit-center">
  <el-pagination
      v-model:currentPage="pageInfo.currentPage"
      v-model:page-size="pageInfo.pageSize"
      :page-sizes="[6, 8 ,10]"
      layout="total, sizes, prev, pager, next, jumper"
      :total="pageInfo.total"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
  />
  </div>

</template>


<script>
import axios from 'axios'
export default {
  name: "ManGrades",

  data() {
    return {
      value:'',
      newValue:'',
      options:[
        {
          value: 'department',
          label: '系别',
        },
        {
          value: 'major',
          label: '专业',
        },
        {
          value: 'class',
          label: '班级',
        }
        ],
      tableData: [],
      range:'',

      pageInfo: {
        currentPage: 1,
        pageSize: 6,
        total: 0,
        CName:''
      },

    }
  },
  created() {
    this.getAll()
  },

  methods: {
    handleSizeChange(pageSize) {
      this.pageInfo.pageSize = pageSize
      this.getAll()
    },

    handleCurrentChange(currentPage) {
      this.pageInfo.currentPage = currentPage
      this.getAll()
    },

    getAll() {

      let param;

      param = "?CName=" + this.pageInfo.CName
      param += "&currentPage=" + this.pageInfo.currentPage
      param += "&pageSize=" + this.pageInfo.pageSize
      param += "&condition=" + this.value

      this.newValue=this.value
      axios.get("/managers/gradeCount" + param).then((res) => {
        console.log(res.data)
        this.pageInfo.currentPage = res.data.data.current
        this.pageInfo.pageSize = res.data.data.size
        this.pageInfo.total = res.data.data.total
        this.tableData=res.data.data.records

      })

    },


  }
}
</script>

<style scoped>
.center{
  width: 100%;
  display:flex;
  justify-content:space-around
}
.el-pagination{
  width: max-content;
  margin-top:20px;
}

</style>