<template>
  <div :style="background" class="bgBackground">
  <div className="center">
    <br><br> <br><br>
    <h1>登录</h1>
    <br><br>
    <el-input
        v-model="formLabelAlign.username"
        placeholder="userID"
        style="width:300px; height:40px"
        clearable="true"

    />
    <br><br>
    <el-input
        v-model="formLabelAlign.password"
        type="password"
        style="width:300px; height:40px"
        placeholder="password"
        show-password
        clearable="true"
    />
    <br><br> <br><br>



    <el-dropdown>
    <span class="el-dropdown-link">
      请选择登录身份
      <el-icon class="el-icon--right">
        <arrow-down />
      </el-icon>
    </span>
      <template #dropdown>
        <el-dropdown-menu>
          <el-dropdown-item @click="StuLogin" style="font-size:20px">学生登录</el-dropdown-item>
          <el-dropdown-item @click="TeaLogin" style="font-size:20px">老师登录</el-dropdown-item>
          <el-dropdown-item @click="ManLogin" style="font-size:20px">管理员登录</el-dropdown-item>
        </el-dropdown-menu>
      </template>
    </el-dropdown>
  </div>

  </div>
</template>


<script>
import axios from 'axios'
export default {
  name: "LoGin",
  data() {
    return {
      formLabelAlign:
          {
            username: "",
            password: "",
            name:""
          },

      background: {
        // 背景图片地址
        backgroundImage: 'url(' + require('@/assets/blue.jpg') + ')',
        // 背景图片是否重复
        backgroundRepeat: 'no-repeat',
        // 背景图片大小
        backgroundSize: 'cover',
        // 背景颜色
        backgroundColor: '#000',
        // 背景图片位置
        backgroundPosition: 'center top'
      }

    }
  },
  methods: {

    StuLogin(){
      if(this.formLabelAlign.username==null||this.formLabelAlign.password==null) this.$message.info("学号或密码都不能为空哦")
      else{
        axios.post("/students/login"+"?username="+this.formLabelAlign.username+"&password="+this.formLabelAlign.password).then((res)=> {
          if (res.data.flag) {
            console.log(res.data)
            this.formLabelAlign.name=res.data.data
            sessionStorage.setItem("student", this.formLabelAlign.username)
            sessionStorage.setItem("stuName", this.formLabelAlign.name)
            this.$alert("登录成功！  亲爱的" + sessionStorage.getItem("stuName") + "同学，欢迎您！", "温馨提示", {
              confirmButtonText: '知道啦'
            })
            this.$router.push('/stu-login');
          }

          else {
            console.log(res.data)
            this.$alert(res.data.msg, "温馨提示", {
              confirmButtonText: '知道啦'
            })
          }
        })
      }

    },

    TeaLogin(){
      if(this.formLabelAlign.username==null||this.formLabelAlign.password==null) this.$message.info("学号或密码都不能为空哦")
      else{
        axios.post("/teachers/login"+"?username="+this.formLabelAlign.username+"&password="+this.formLabelAlign.password).then((res)=> {
          if (res.data.flag) {
            console.log(res.data)
            this.formLabelAlign.name=res.data.data
            sessionStorage.setItem("teaName", this.formLabelAlign.name)
            sessionStorage.setItem("teacher", this.formLabelAlign.username)
            this.$alert("登录成功！  尊敬的" + sessionStorage.getItem("teaName") + "老师，欢迎您！", "温馨提示", {
              confirmButtonText: '知道啦'
            })
            this.$router.push('/tea-login');
          }
          else {
            console.log(res.data)
            this.$alert(res.data.msg, "温馨提示", {
              confirmButtonText: '知道啦'
            })
          }
        })
      }

    },

    ManLogin(){
      if(this.formLabelAlign.username==null||this.formLabelAlign.password==null) this.$message.info("学号或密码都不能为空哦")
      else{
        axios.post("/managers/login"+"?username="+this.formLabelAlign.username+"&password="+this.formLabelAlign.password).then((res)=> {
          if (res.data.flag) {
            console.log(res.data)
            this.formLabelAlign.name=res.data.data
            sessionStorage.setItem("manName", this.formLabelAlign.name)
            this.$alert("登录成功！  尊敬的" + sessionStorage.getItem("manName") + "管理员，欢迎您！", "温馨提示", {
              confirmButtonText: '知道啦'
            })
            this.$router.push('/man-login');
          }

          else {
            console.log(res.data)
            this.$alert(res.data.msg, "温馨提示", {
              confirmButtonText: '知道啦'
            })
          }
        })
      }

    }

  }
}
</script>


<style scoped>
.center {
  text-align: center;
}

.example-showcase {

  cursor: pointer;
  color: #409EFF;
  display: flex;
  align-items: center;

}

.el-dropdown-link {
  font-size:25px;
  cursor: pointer;
  color: #07fa07;
  display: flex;
  align-items: center;
}

.bgBackground{
  width:100%;
  height:765px;
}

</style>

