<template>
  <el-input v-model="pageInfo.queryId"  style="width:250px ;height:40px" placeholder="请输入学号(例：2020022)" clearable="true"/>
  <el-button  @click="getAll" style="height:38px" type="primary">
    <el-icon><Search/></el-icon>
  </el-button>

  <el-button type="success" @click="add" plain>添加</el-button>
  <br><br>


  <el-dialog v-model="dialogFormVisible" title="新增学生" width="30%">
    <el-form :model="Student" >
      <el-form-item label="学生id" :label-width="formLabelWidth" >
        <el-input v-model="Student.s_id" placeholder="请输入学生id" autocomplete="off" />
      </el-form-item>
        <el-form-item label="学生姓名" :label-width="formLabelWidth">
          <el-input v-model="Student.s_name" placeholder="请输入学生姓名" autocomplete="off" />
        </el-form-item>
      <el-form-item label="学生性别" :label-width="formLabelWidth">
        <el-select v-model="Student.s_sex" placeholder="Please select">
          <el-option label="男" value="男" />
          <el-option label="女" value="女" />
        </el-select>
      </el-form-item>
      <el-form-item label="学生密码" :label-width="formLabelWidth">
        <el-input v-model="Student.s_password" placeholder="请输入学生密码" autocomplete="off" />
      </el-form-item>
        <el-form-item label="学生系别" :label-width="formLabelWidth">
          <el-input v-model="Student.s_department"  placeholder="请输入学生系别" autocomplete="off" />
        </el-form-item>
      <el-form-item label="学生专业" :label-width="formLabelWidth">
        <el-input v-model="Student.s_major" placeholder="请输入学生专业" autocomplete="off" />
      </el-form-item>
      <el-form-item label="学生班级" :label-width="formLabelWidth">
        <el-input v-model="Student.s_class" placeholder="请输入学生班级" autocomplete="off" />
      </el-form-item>
    </el-form>
    <template #footer>
      <span class="dialog-footer">
        <el-button type="info" @click="cancel">取消</el-button>
        <el-button type="success" @click="handleAdd">确认</el-button>
      </span>
    </template>
  </el-dialog>


  <el-dialog v-model="dialogFormVisibleEdit" title="编辑学生信息" width="30%">
    <el-form :model="Student" >
      <el-form-item label="学生id" :label-width="formLabelWidth">
        <el-input v-model="Student.s_id" placeholder="请输入学生id" autocomplete="off" disabled="true"/>
      </el-form-item>
      <el-form-item label="学生姓名" :label-width="formLabelWidth">
        <el-input v-model="Student.s_name" placeholder="请输入学生姓名" autocomplete="off" />
      </el-form-item>
      <el-form-item label="学生性别" :label-width="formLabelWidth">
        <el-select v-model="Student.s_sex" placeholder="Please select">
          <el-option label="男" value="男" />
          <el-option label="女" value="女" />
        </el-select>
      </el-form-item>
      <el-form-item label="学生密码" :label-width="formLabelWidth">
        <el-input v-model="Student.s_password" placeholder="请输入学生密码" autocomplete="off" />
      </el-form-item>
      <el-form-item label="学生系别" :label-width="formLabelWidth">
        <el-input v-model="Student.s_department"  placeholder="请输入学生系别" autocomplete="off" />
      </el-form-item>
      <el-form-item label="学生专业" :label-width="formLabelWidth">
        <el-input v-model="Student.s_major" placeholder="请输入学生专业" autocomplete="off" />
      </el-form-item>
      <el-form-item label="学生班级" :label-width="formLabelWidth">
        <el-input v-model="Student.s_class" placeholder="请输入学生班级" autocomplete="off" />
      </el-form-item>
    </el-form>
    <template #footer>
      <span class="dialog-footer">
        <el-button type="info" @click="cancel">取消</el-button>
        <el-button type="success" @click="handleUpdate">确认</el-button>
      </span>
    </template>
  </el-dialog>


  <el-table :data="tableData" stripe style="width: 100%">
    <el-table-column prop="s_id" label="学生id" align="center"/>
    <el-table-column prop="s_name" label="学生姓名" align="center"/>
    <el-table-column prop="s_sex" label="性别" align="center"/>
    <el-table-column prop="s_password" label="密码" align="center"/>
    <el-table-column prop="s_department" label="系别" width="250px" align="center"/>
    <el-table-column prop="s_major" label="专业名称" align="center"/>
    <el-table-column prop="s_class" label="班级名称" align="center"/>

    <el-table-column   label="操作" align="center">

      <template #default="scope">
        <el-button size="small" type="info" @click="handleEdit(scope.row)">编辑</el-button>
        <el-button size="small" type="danger" @click="handleDelete(scope.row)">删除</el-button>
      </template>

    </el-table-column>
  </el-table>

<div style="text-align: -webkit-center">
  <el-pagination
      v-model:currentPage="pageInfo.currentPage"
      v-model:page-size="pageInfo.pageSize"
      :page-sizes="[6, 8, 10]"
      layout="total, sizes, prev, pager, next, jumper"
      :total="pageInfo.total"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
  />
</div>

</template>

<script>
import axios from 'axios'
export default {
  name: "ManStudent",
  data(){
    return{
      dialogFormVisible:false,
      dialogFormVisibleEdit:false,
      Student:{},
      formLabelWidth:'140px',
      tableData:[],

      pageInfo: {
        currentPage: 1,
        pageSize: 10,
        total:0,
        queryId:''
      },
    }
  },

  created(){
    this.getAll()
  },


  methods:{

    cancel(){
      this.dialogFormVisible=false
      this.dialogFormVisibleEdit=false
      this.$message.info("已取消当前操作")
    },

    getAll(){

      let param;
      param="?currentPage="+this.pageInfo.currentPage
      param +="&pageSize="+this.pageInfo.pageSize
      param +="&s_id="+this.pageInfo.queryId


      axios.get("/managers/Stu/"+param).then((res)=>
      {
        console.log(res)
        this.pageInfo.currentPage=res.data.data.current
        this.pageInfo.pageSize=res.data.data.size
        this.pageInfo.total=res.data.data.total

        this.tableData =res.data.data.records

      })
    },
    resetForm(){
     this.Student={}
    },
//弹出添加窗口
    add(){
      this.dialogFormVisible = true
      this.resetForm()
    },
//确认添加
    handleAdd(){
      if(this.Student.s_id==null||this.Student.s_name==null||this.Student.s_sex==null||this.Student.s_password==null||this.Student.s_department==null ||
          this.Student.s_major==null||this.Student.s_class==null)
        this.$message.error("添加内容都不能为空哦!")

      else
      {axios.post("/managers/saveStu",this.Student).then(res=>{
            console.log(res)
            if(res.data.flag)
            { this.dialogFormVisible=false;
              this.$message.success("添加成功！")
            }
            else
              this.$message.error(res.data.msg)
          }
      ).finally(()=>{
        this.getAll()
      })
    }},

//弹出编辑窗口
    handleEdit(row){
      axios.get("/managers/getOneStu/?sid="+row.s_id).then((res)=>{
        if(res.data.flag){
          this.Student=res.data.data
          this.dialogFormVisibleEdit=true;
        }
        else{
          this.$message.error("数据同步失败，自动刷新");
        }
      }).finally(()=>{
        this.getAll();
      })
    },

//编辑后确认
    handleUpdate(){
      axios.put("/managers/updateStu",this.Student).then(res=>{
            console.log(res)
            if(res.data.flag) {
              this.$message.success("更新成功！")
              this.dialogFormVisibleEdit = false
            }
            else
              this.$message.error(res.data.msg)
          }
      ).finally(()=>{
        this.getAll()
      })
    },

    handleDelete(row) {
      this.$confirm('此操作将永久删除该文件, 是否继续?', '温馨提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        axios(
            {
              url:"/managers/deleteStu",
              method:"DELETE",
              data:{s_id:row.s_id}
            }
        ).then((res) => {
              if (res.data.flag)
                this.$message.success('删除成功!');
              else
                this.$message.error(res.data.msg);
            }
        ).finally(() => {
              this.getAll()
            }
        )
      }).catch(() => {
        this.$message.info('已取消删除');
      })
    },


      handleSizeChange(pageSize) {
      this.pageInfo.pageSize=pageSize
      this.getAll()
    },

    handleCurrentChange(currentPage) {
      this.pageInfo.currentPage=currentPage
      this.getAll()
    }

  }
}

</script>

<style scoped>
.el-pagination{
  width: max-content;
  margin-top:20px;
}
</style>