<template>
  <el-input v-model="pageInfo.queryId" style="width:250px ;height:40px" placeholder="请输入课程id（例：31691)" clearable="true"/>
  <el-button  @click="getAll" style="height:38px" type="primary">
    <el-icon><Search/></el-icon>
  </el-button>

  <el-button type="success" @click="add" plain>添加</el-button>
  <br><br>

  <el-dialog v-model="dialogFormVisible" title="新增课程" width="30%">
    <el-form :model="Class">
      <el-form-item label="课程id" :label-width="formLabelWidth" >
        <el-input v-model="Class.c_id" placeholder="请输入课程id" autocomplete="off" />
      </el-form-item>

      <el-form-item label="课程名称" :label-width="formLabelWidth">
        <el-input v-model="Class.c_name" placeholder="请输入课程名称" autocomplete="off" />
      </el-form-item>


      <el-form-item label="学期" :label-width="formLabelWidth">
        <el-input v-model="Class.term"  placeholder="请输入学期" autocomplete="off" />
      </el-form-item>

      <el-form-item label="学分" :label-width="formLabelWidth">
        <el-input v-model="Class.credit" placeholder="请输入课程学分" autocomplete="off" />
      </el-form-item>

      <el-form-item label="教师" :label-width="formLabelWidth">
        <el-input v-model="Class.t_id" placeholder="请输入授课老师" autocomplete="off" />
      </el-form-item>

    </el-form>

    <template #footer>
      <span class="dialog-footer">
        <el-button type="info" @click="cancel">取消</el-button>
        <el-button type="success" @click="handleAdd">确认</el-button>
      </span>
    </template>
  </el-dialog>



  <el-dialog v-model="dialogFormVisibleEdit" title="编辑课程信息" width="30%">
    <el-form :model="Class">
      <el-form-item label="课程id" :label-width="formLabelWidth">
        <el-input v-model="Class.c_id" placeholder="请输入课程id" autocomplete="off" disabled="true"/>
      </el-form-item>

      <el-form-item label="课程名称" :label-width="formLabelWidth">
        <el-input v-model="Class.c_name" placeholder="请输入课程名称" autocomplete="off" />
      </el-form-item>


      <el-form-item label="学期" :label-width="formLabelWidth">
        <el-input v-model="Class.term"  placeholder="请输入学期" autocomplete="off" />
      </el-form-item>

      <el-form-item label="学分" :label-width="formLabelWidth">
        <el-input v-model="Class.credit" placeholder="请输入课程学分" autocomplete="off" />
      </el-form-item>

      <el-form-item label="教师" :label-width="formLabelWidth">
        <el-input v-model="Class.t_id" placeholder="请输入授课老师" autocomplete="off" />
      </el-form-item>

    </el-form>

    <template #footer>
      <span class="dialog-footer">
        <el-button type="info" @click="cancel">取消</el-button>
        <el-button type="success" @click="handleUpdate">确认</el-button>
      </span>
    </template>
  </el-dialog>



  <el-table :data="tableData" stripe style="width: 100%">
    <el-table-column prop="c_id" label="课程id"  align="center"/>
    <el-table-column prop="c_name" label="课程名称" align="center" />
    <el-table-column prop="term" label="学期" align="center"/>
    <el-table-column prop="credit" label="学分" align="center"/>
    <el-table-column prop="t_id" label="教师" align="center"/>
    <el-table-column label="操作" align="center">

      <template #default="scope">
        <el-button size="small" type="info" @click="handleEdit(scope.row)">编辑</el-button>
        <el-button size="small" type="danger" @click="handleDelete(scope.row)">删除</el-button>
      </template>

    </el-table-column >
  </el-table>

  <div style="text-align: -webkit-center">
  <el-pagination
      v-model:currentPage="pageInfo.currentPage"
      v-model:page-size="pageInfo.pageSize"
      :page-sizes="[6, 8, 10]"
      layout="total, sizes, prev, pager, next, jumper"
      :total="pageInfo.total"
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
  />
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: "ManClass",
  data(){
    return{
      dialogFormVisible:false,
      dialogFormVisibleEdit:false,
      Class:{},
      formLabelWidth:'140px',

      tableData:[],

      pageInfo: {
        currentPage: 1,
        pageSize: 10,
        total:0,
        queryId:''
      },

    }
  },

  created(){
    this.getAll()
  },


  methods:{

    cancel(){
      this.dialogFormVisible=false
      this.dialogFormVisibleEdit=false
      this.$message.info("已取消当前操作")
    },

    getAll(){

      let param;
      param="?currentPage="+this.pageInfo.currentPage
      param +="&pageSize="+this.pageInfo.pageSize
      param +="&c_id="+this.pageInfo.queryId


      axios.get("/managers/Cou/"+param).then((res)=>
      {
        console.log(res)
        this.pageInfo.currentPage=res.data.data.current
        this.pageInfo.pageSize=res.data.data.size
        this.pageInfo.total=res.data.data.total

        this.tableData =res.data.data.records

      })
    },
    resetForm(){
      this.Class={}
    },
//弹出添加窗口
    add(){
      this.dialogFormVisible = true
      this.resetForm()
    },
//确认添加
    handleAdd(){
      if(this.Class.c_id==null||this.Class.c_name==null||this.Class.credit==null||this.Class.term==null)
        this.$message.error("添加内容都不能为空哦!")
      else
      {axios.post("/managers/saveCou",this.Class).then(res=>{
            console.log(res)
            if(res.data.flag)
            { this.dialogFormVisible=false;
              this.$message.success("添加成功！")
            }
            else
              this.$message.error(res.data.msg)
          }
      ).finally(()=>{
        this.getAll()
      })
    }},

//弹出编辑窗口
    handleEdit(row){
      axios.get("/managers/getOneCou/?cid="+row.c_id).then((res)=>{
        if(res.data.flag){
          this.Class=res.data.data
          this.dialogFormVisibleEdit=true;
        }
        else{
          this.$message.error("数据同步失败，自动刷新");
        }
      }).finally(()=>{
        this.getAll();
      })
    },

//编辑后确认
    handleUpdate(){
      axios.put("/managers/updateCou",this.Class).then(res=>{
            console.log(res)
            if(res.data.flag) {
              this.$message.success("更新成功！")
              this.dialogFormVisibleEdit = false
            }
            else
              this.$message.error(res.data.msg)
          }
      ).finally(()=>{
        this.getAll()
      })
    },

    handleDelete(row) {
      this.$confirm('此操作将永久删除该文件, 是否继续?', '温馨提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        axios(
            {
              url:"/managers/deleteCou",
              method:"DELETE",
              data:{c_id:row.c_id}
            }
        ).then((res) => {
              if (res.data.flag)
                this.$message.success('删除成功!');
              else
                this.$message.error(res.data.msg);
            }
        ).finally(() => {
              this.getAll()
            }
        )
      }).catch(() => {
        this.$message.info('已取消删除');
      })
    },


    handleSizeChange(pageSize) {
      this.pageInfo.pageSize=pageSize
      this.getAll()
    },

    handleCurrentChange(currentPage) {
      this.pageInfo.currentPage=currentPage
      this.getAll()
    }

  }
}

</script>
<style scoped>
.el-pagination{
  width: max-content;
  margin-top:20px;
}
</style>