<template>
  <div class="center">
    <el-card  style="width: 500px">
      <el-form-item label="请输入旧密码">
        <el-input  type="password" v-model="passwords.Pwd1" placeholder="previous password" clearable="true" show-password/>
      </el-form-item>

      <el-form-item label="请输入新密码">
        <el-input  type="password" v-model="passwords.Pwd2" placeholder="new password" clearable="true" show-password/>
      </el-form-item>

      <el-form-item label="请确认新密码">
        <el-input   type="password" v-model="passwords.Pwd3" placeholder="Please confirm" clearable="true" show-password/>
      </el-form-item>


      <el-button type="primary" plain @click="change">确认修改</el-button>

    </el-card>
  </div>

</template>

<script>
import axios from 'axios'
export default {
  name: "ManPassword",
  data(){
    return{
      passwords:{
        Pwd1:"",
        Pwd2:"",
        Pwd3:""
      }
    }
  },
  methods: {
    change() {

      if (this.passwords.Pwd1 === '' || this.passwords.Pwd2 === '' || this.passwords.Pwd3 === '') {
        this.$alert("密码都不能为空哦！", "温馨提示", {
          confirmButtonText: '知道啦'
        });
      } else if (this.passwords.Pwd2 !== this.passwords.Pwd3) {
        this.$alert("请确认两次密码相同哦", "温馨提示", {
          confirmButtonText: '知道啦'
        });
      } else {
        axios(
            {
              url: "/managers/alterPassword?oldPassword="+this.passwords.Pwd1+"&newPassword="
                  +this.passwords.Pwd2,
              method: "PUT",
              data: {
                userId: sessionStorage.getItem("stuName")
              }
            }).then(res => {
              console.log(res.data)

          if (!res.data.flag)
          {
            this.$alert(res.data.msg, "温馨提示", {
              confirmButtonText: '知道啦'
            })
          }
          else {
              this.$message.success( '修改密码成功！')
            this.passwords.Pwd1=''
            this.passwords.Pwd2=''
            this.passwords.Pwd3=''
          }})
      }
    }}
}
</script>

<style scoped>
.center{
  width: 100%;
  display:flex;
  justify-content:space-around;
}
</style>

