<template>
  <div class="center">
    <el-card style="width: 500px">
      <el-form-item label="请输入旧密码">
        <el-input  type="password" clearable="true" v-model="passwordForm.Pwd1" placeholder="previous password" show-password/>
      </el-form-item>

      <el-form-item label="请输入新密码">
        <el-input  type="password" clearable="true" v-model="passwordForm.Pwd2" placeholder="new password" show-password/>
      </el-form-item>

      <el-form-item label="请确认新密码">
        <el-input   type="password" clearable="true" v-model="passwordForm.Pwd3" placeholder="Please confirm" show-password/>
      </el-form-item>

      <el-button type="primary" plain @click="confirm">确认修改</el-button>
    </el-card>

    <el-card style="width: 500px">
      <el-form-item label="请输入新电话">
        <el-input  clearable="true"  v-model="newTel" placeholder="new telephone" />
      </el-form-item>

      <el-button type="primary" plain @click="changeTel">确认修改</el-button>
    </el-card>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: "TeaPassword",
  data() {
    return {
      passwordForm: {
        Pwd1: '',
        Pwd2: '',
        Pwd3: '',
      },


        newTel:''

    }
  },

  methods: {
    changeTel(){
      axios.put("/teachers/alterTel?newTel="+this.newTel).then((res)=>{
        console.log(res.data)
        if(res.data.flag){
          this.$message.success("修改电话成功")
          this.newTel=''
        }
        else{
          this.$message.error("新电话和旧电话不能相同哦！")
        }
      })

    },

    confirm() {
      if (this.passwordForm.Pwd1 === '' || this.passwordForm.Pwd2 === '' || this.passwordForm.Pwd3 === '') {
        this.$alert("密码都不能为空哦！", "温馨提示", {
          confirmButtonText: '知道啦'
        });
      } else if (this.passwordForm.Pwd2 !== this.passwordForm.Pwd3) {
        this.$alert("请确认两次密码相同哦", "温馨提示", {
          confirmButtonText: '知道啦'
        });
      } else {
        axios(
            {
              url: "/teachers/alterPassword?oldPassword="+this.passwordForm.Pwd1+"&newPassword="+
                  this.passwordForm.Pwd2,
              method: "PUT",
              data: {
                userId: sessionStorage.getItem("teaName")
              }
            }).then(res => {
          console.log(res.data)
          if (!res.data.flag) {
            this.$alert(res.data.msg, "温馨提示", {
              confirmButtonText: '知道啦'
            })
          } else {
           this.$message.success("修改密码成功！")
            this.passwordForm.Pwd1=''
            this.passwordForm.Pwd2=''
            this.passwordForm.Pwd3=''
            }
          }
        )
      }
    }
    }
  }
</script>



<style scoped>
.center{
  width: 100%;
  display:flex;
  justify-content:space-around;
}
</style>