<template>
  <div>
    <el-container>
      <el-header>
        <h3 >学生成绩在线发布系统</h3>
      </el-header>

      <el-container>

        <el-aside>
          <el-menu
              router
              :default-active="$route.path"
              class="menu"
          >
            <br><br>

            <el-menu-item   index="/tea-main" >
              <el-icon><User /></el-icon>
              <span >首页</span>
            </el-menu-item>
            <br>

            <el-menu-item index="/tea-student" >
              <el-icon><OfficeBuilding /></el-icon>
              <span >学生成绩管理</span>
            </el-menu-item>

            <br>
            <el-menu-item  index="/tea-grades" >
              <el-icon><Reading /></el-icon>
              <span >查询成绩分布</span>
            </el-menu-item>
            <br>

            <el-menu-item  index="/tea-password" >
              <el-icon><Unlock /></el-icon>
              <span >修改电话和密码</span>
            </el-menu-item>
            <br>

            <el-menu-item>
              <el-icon><Close/></el-icon>
              <span @click="logout" >退出登录</span>
            </el-menu-item>

            <br><br>
          </el-menu>
        </el-aside>

        <el-main>
          <router-view></router-view>

        </el-main>

      </el-container>

    </el-container>

  </div>
</template>



<script>
import axios from 'axios'

export default {
  name: '"TeaLogin"',
  components: {},
  methods: {
    logout() {
      this.$confirm('确认要退出吗？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: ''
      }).then(() => {
        axios.post("/teachers/logout").then((res) => {
          console.log(res.data)
          if (res.data.flag) {
            this.$router.push("/login")
            this.$message.success("退出登录成功")
          } else {
            this.$message.error(res.data.msg)
          }
        })
        //sessionStorage.clear();
      })
    }
  }
}
</script>

<style scoped>
