<template>

  <el-select v-model="c_id" style="width:200px" size="large" placeholder="请选择课程名">
    <el-option key=""  value=""  label="全部课程"/>
    <el-option
        v-for="item in options"
        :key="item.c_id"
        :label="item.c_name"
        :value="item.c_id"
    />
  </el-select>

  <el-input v-model="pageInfo.s_id"  style="width:120px ;height:40px" clearable="true" placeholder="请输入学号" />
  <el-button  @click="getAll" style="height:38px" type="primary">
    <el-icon><Search/></el-icon>
  </el-button>

  <el-button type="success" @click="add" plain>添加</el-button>
  <el-button type="danger" plain v-print="printConfig">打印</el-button>
  <el-upload action="http://localhost:8081/teachers/ExcelImport"
             with-credentials:true
             :data="{tid:sessionTid}"
             multiple:headers="'Content-Type':'multipart/form-data‘"
             style="display: inline-block"
             :show-file-list="false"
             accept="xlsx"
             :on-success="handleExelImportSuccess">
    <el-button type="primary" plain >导入<el-icon><Download /></el-icon></el-button>
  </el-upload>

    <el-dialog v-model="isShow" title="导入的具体错误" width="30%">
    <el-scrollbar max-height="200px">
      <p v-for="item in excelData" :key="item" class="scrollbar-demo-item">
        {{ item }}
      </p>
    </el-scrollbar>
    </el-dialog>

  <el-dialog v-model="isShow2" title="导入的具体情况" width="30%">
    <el-scrollbar max-height="200px">
      <p v-for="item in excelData" :key="item" class="scrollbar-demo-item">
        {{ item }}
      </p>
    </el-scrollbar>
  </el-dialog>

  <br><br>

  <el-dialog v-model="dialogFormVisible" title="新增学生成绩" width="30%">
    <el-form :model="form">
      <el-form-item label="学生id" :label-width="formLabelWidth" >
        <el-input v-model="form.s_id" placeholder="请输入学生id" autocomplete="off" />
      </el-form-item>

      <el-form-item label="学生班级" :label-width="formLabelWidth">
        <el-input v-model="form.s_class" placeholder="请输入学生班级" autocomplete="off" />
      </el-form-item>

      <el-form-item label="学生姓名" :label-width="formLabelWidth">
        <el-input v-model="form.s_name" placeholder="请输入学生姓名" autocomplete="off" />
      </el-form-item>

      <el-form-item label="学生成绩" :label-width="formLabelWidth">
        <el-input v-model="form.score" placeholder="请输入学生成绩" autocomplete="off" />
      </el-form-item>

      <el-form-item label="课程名" :label-width="formLabelWidth">
        <el-input v-model="form.c_name" placeholder="请输入课程名" autocomplete="off" />
      </el-form-item>

      <el-form-item label="课程id" :label-width="formLabelWidth">
        <el-input v-model="form.c_id"  placeholder="请输入课程id" autocomplete="off" />
      </el-form-item>
    </el-form>

    <template #footer>
      <span class="dialog-footer">
        <el-button type="info" @click="cancel">取消</el-button>
        <el-button type="success" @click="handleAdd">确认</el-button>
      </span>
    </template>
  </el-dialog>

  <el-dialog v-model="dialogFormVisibleEdit" title="编辑学生成绩" width="30%">
    <el-form :model="form">
      <el-form-item label="学生id" :label-width="formLabelWidth" >
        <el-input v-model="form.s_id" placeholder="请输入学生id" autocomplete="off" disabled="true"/>
      </el-form-item>

      <el-form-item label="学生班级" :label-width="formLabelWidth">
        <el-input v-model="form.s_class" placeholder="请输入学生班级" autocomplete="off" disabled="true"/>
      </el-form-item>

      <el-form-item label="学生姓名" :label-width="formLabelWidth">
        <el-input v-model="form.s_name" placeholder="请输入学生姓名" autocomplete="off" disabled="true"/>
      </el-form-item>

      <el-form-item label="学生成绩" :label-width="formLabelWidth">
        <el-input v-model="form.score" placeholder="请输入学生成绩" autocomplete="off" />
      </el-form-item>

      <el-form-item label="课程名" :label-width="formLabelWidth">
        <el-input v-model="form.c_name" placeholder="请输入课程名" autocomplete="off" disabled="true"/>
      </el-form-item>

      <el-form-item label="课程id" :label-width="formLabelWidth">
        <el-input v-model="form.c_id"  placeholder="请输入课程id" autocomplete="off" disabled="true"/>
      </el-form-item>

    </el-form>

    <template #footer>
      <span class="dialog-footer">
        <el-button type="info" @click="cancel">取消</el-button>
        <el-button type="success" @click="handleUpdate">确认</el-button>
      </span>
    </template>
  </el-dialog>

  <div id="printPage">
    <el-table :data="tableData" stripe style="width: 100%">

      <el-table-column prop="s_id" label="学生id" align="center" width="180px"/>
      <el-table-column prop="s_class" label="班级名称" align="center" width="180px"/>
      <el-table-column prop="s_name" label="学生姓名" align="center" width="160px"/>
      <el-table-column prop="score" label="学生成绩" align="center" width="150px"/>
      <el-table-column prop="c_name" label="课程名" width="250px" align="center"/>
      <el-table-column prop="c_id" label="课程id" align="center" width="150px"/>
      <el-table-column  label="操作" align="center" width="200px">
        <template #default="scope" >
          <el-button size="small" type="info" @click="handleEdit(scope.row)">编辑</el-button>
          <el-button size="small" type="danger" @click="handleDelete(scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>

  <div style="text-align: -webkit-center">
    <el-pagination
        v-model:currentPage="pageInfo.currentPage"
        v-model:page-size="pageInfo.pageSize"
        :page-sizes="[9,10,11,pageInfo.total]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="pageInfo.total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
    />
  </div>

</template>

<script>
import axios from 'axios'
export default {
  name: "teaStudent",
  data(){
    return{
      isShow:false,
      isShow2:false,
      excelData:[],
      printConfig:{
        id:'printPage', //被打印部分的id
        popTitle:''
      },
      sessionTid:'',
      dialogFormVisible:false,
      dialogFormVisibleEdit:false,
      form:{},
      formLabelWidth:'140px',
      tableData:[],

      c_id:'',
      c_name:'',
      options:[],

      pageInfo: {
        currentPage: 1,
        pageSize: 10,
        total:0,
        s_id:''
      },
    }
  },

  created(){
    this.getAll()

    axios.get("/teachers/myCourse").then((res)=>{
      console.log(res.data)
      this.options=res.data.data
      console.log(res.data.data.c_name)
    })
    this.sessionTid = sessionStorage.getItem("teacher")
  },

  methods:{
    handleExelImportSuccess(res)
    {
      this.excelData = res.data

        if(res.flag) {
          this.$confirm(res.msg+'，是否查看具体情况?', '温馨提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'success'
          }).then(() => {
            console.log(res.data)
            this.isShow2 = true
          }).catch(() => {
                this.$message.info('已取消查看');
              })
        }
    else
      {
        console.log(res.data)
        this.$confirm(res.msg+'，是否查看具体情况?', '温馨提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'error'
        }).then(() => {
            console.log(res.data)
            this.isShow=true
          })
        .catch(() => {
          this.$message.info('已取消查看');})
      }
    this.getAll()
    },

    cancel(){
      this.dialogFormVisible=false
      this.dialogFormVisibleEdit=false
      this.$message.info("已取消当前操作")
    },

    getAll(){

      let param;
      param="?currentPage="+this.pageInfo.currentPage
      param +="&pageSize="+this.pageInfo.pageSize
      param +="&s_id="+this.pageInfo.s_id
      param +="&c_id="+this.c_id


      axios.get("/teachers/courseGrade"+param).then((res)=>
      {
        console.log(res.data)
        this.pageInfo.currentPage=res.data.data.current
        this.pageInfo.pageSize=res.data.data.size
        this.pageInfo.total=res.data.data.total

        this.tableData =res.data.data.records

      })
      this.printConfig.popTitle=this.c_id+"学生成绩表"

    },
    resetForm(){
      this.form={}
    },
//弹出添加窗口
    add(){
      this.dialogFormVisible = true
      this.resetForm()
    },
//确认添加
    handleAdd(){
      if(this.form.s_id==null||this.form.s_name==null||this.form.s_class==null||
          this.form.c_name==null || this.form.score==null||this.form.c_id==null)
        this.$message.error("添加内容都不能为空哦！")
      else
      {
        axios.post("/teachers/insertGrade",this.form).then(res=>{
            if(res.data.flag)
            { this.dialogFormVisible=false;
              this.$message.success("添加成功！")
            }
            else
              this.$message.error(res.data.msg)
          }
      ).finally(()=>{
        this.getAll()
      })
    }
      },

//弹出编辑窗口
    handleEdit(row){
      axios.get("/teachers/getOneGrade?sid="+row.s_id+"&cid="+row.c_id).then((res)=>{
        if(res.data.flag){
          this.form=res.data.data
          this.dialogFormVisibleEdit=true;
        }
        else{
          this.$message.error(res.data.msg);}
      }).finally(()=>{
        this.getAll();})},
//编辑后确认
    handleUpdate(){
      axios.put("/teachers/updateGrade?",this.form).then(res=>{
            console.log(res.data)
            if(res.data.flag) {
              this.$message.success("更新成功！")
              this.dialogFormVisibleEdit = false
            }
            else
              this.$message.error(res.data.msg)
          }
      ).finally(()=>{
        this.getAll()})
    },

    handleDelete(row) {
      this.$confirm('此操作将永久删除该文件, 是否继续?', '温馨提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        axios({
              url: "/teachers/deleteGrade",
              method : "DELETE",
              data:{
                s_id : row.s_id,
                s_class : row.s_class,
                s_name : row.s_name ,
                score : row.score ,
                c_name : row.c_name ,
                c_id :row.c_id
                }
            }).then((res) => {
          console.log(res.data)
              if (res.data.flag) this.$message.success('删除成功!');
              else  this.$message.error(res.data.msg);}
        ).finally(() => {
              this.getAll()})
      }).catch(() => {
        this.$message.info('已取消删除');})},


    handleSizeChange(pageSize) {
      this.pageInfo.pageSize=pageSize
      this.getAll()
    },

    handleCurrentChange(currentPage) {
      this.pageInfo.currentPage=currentPage
      this.getAll()
    }

  }
}

</script>


<style scoped>
.el-pagination{
  width: max-content;
  margin-top:20px;
}
.scrollbar-demo-item {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 20px;
  margin: 10px;
  text-align: center;
  border-radius: 4px;
}
/deep/table{
  table-layout: fixed !important;
}
/deep/.el-table__header-wrapper .el-table__header{
  width: 100% !important;
}
/deep/.el-table__body-wrapper .el-table__body{
  width: 100% !important;
}
</style>

<!--/deep/--->
<!--打印时添加的样式-->
<style scoped media="print">
/*指定打印的分页*/
/deep/ section{
  page-break-after:always;
}

/*避免出现空白尾页*/
html,body,#app{
  height: 98% !important;
  margin: 0px;
  padding: 0px;
}

</style>