<template>
  <el-input v-model="pageInfo.queryTerm" style="height:40px"  placeholder="请输入学期（例：2021s)" clearable="true"/>
  <el-button  @click="getAll" style="height:38px" type="primary">
    <el-icon><Search/></el-icon>
  </el-button>
  <el-button type="danger" plain v-print="printConfig">打印</el-button>

  <br><br>
  <div id="printPage">

  <el-table :data="tableData" style="width: 100%">
    <el-table-column prop=c_name label="课程名" align="center" />
    <el-table-column prop=score label="分数" align="center" />
    <el-table-column prop=credit label="学分" align="center"/>
    <el-table-column prop=term label="学期" align="center"/>
  </el-table>

  </div>
  <div style="text-align: -webkit-center">
    <el-pagination
        v-model:currentPage="pageInfo.currentPage"
        v-model:page-size="pageInfo.pageSize"
        :page-sizes="[6, 8,10,pageInfo.total]"
        layout="total, sizes, prev, pager, next, jumper"
        :total="pageInfo.total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
    />
  </div>

</template>


<script>
import axios from 'axios'
export default {
  name: "StuGrades",

  data() {
    return {
      tableData: [],

      pageInfo: {
        currentPage: 1,
        pageSize: 10,
        total: 0,
        queryTerm: '',
      },

      printConfig:{
        id:'printPage', //被打印部分的id
        popTitle:'成绩单'
      },
    }
  },
  created() {
    this.getAll()
  },

  methods: {
    handleSizeChange(pageSize) {
      this.pageInfo.pageSize = pageSize
      this.getAll()
    },

    handleCurrentChange(currentPage) {
      this.pageInfo.currentPage = currentPage
      this.getAll()
    },

    getAll() {

      let param;
      param = "?currentPage=" + this.pageInfo.currentPage
      param += "&pageSize=" + this.pageInfo.pageSize
      param += "&term=" + this.pageInfo.queryTerm


      axios.get("/students/myGrade" + param).then((res) => {
        console.log(res.data)
        this.pageInfo.currentPage = res.data.data.current
        this.pageInfo.pageSize = res.data.data.size
        this.pageInfo.total = res.data.data.total

        this.tableData = res.data.data.records

      })
      this.printConfig.popTitle=sessionStorage.getItem("student")+" "+sessionStorage.getItem("stuName")+" 成绩单"
    },


  }
}
</script>

<style scoped >
.el-input{
  text-align: center;
  width: 300px;
  height: 40px;
}
.el-pagination{
  width: max-content;
  margin-top:20px;
}

/deep/ table{
  table-layout: auto !important;
}
/deep/ .el-table__header-wrapper .el-table__header{
  width: 100% !important;
}
/deep/ .el-table__body-wrapper .el-table__body{
  width: 100% !important;
}

</style>