<template>
  <div class="center">
    <el-card>
      <h2>查询成绩分布</h2>
      <br><br>
      <el-select v-model="c_id" style="width:200px" size="large" placeholder="请选择所教授课程名">
        <el-option
            v-for="item in options"
            :key="item.c_id"
            :label="item.c_name"
            :value="item.c_id"
        />
      </el-select>

      <el-button type="primary" @click="query" size="large">查询</el-button>

    </el-card>

    <el-card>
      <div id="main" style="width: 600px;height:400px;"></div>


      <div class="center">

        <el-form-item style="width: 150px" label="最高分">
          <el-input v-model="highestScore"  disabled="true"/>
        </el-form-item>

        <el-form-item style="width: 150px" label="最低分">
          <el-input v-model="lowestScore" disabled="true" />
        </el-form-item>

      </div>
      <div class="center">

        <el-form-item  label="平均分">
          <el-input style="width: 100px" v-model="averageScore" disabled="true" />
        </el-form-item>
        <el-form-item style="width: 150px" label="不及格人数">
          <el-input v-model="list.failCount"  disabled="true"/>
        </el-form-item>

      </div>

    </el-card>

  </div>
</template>

<script>
import * as echarts from 'echarts'
import axios from 'axios'
export default {

  name: "TeaGrades",
  data() {
    return {
      c_id: '',
      options:[],

      list: {
        b90_100Count: 0,
        b80_89Count: 0,
        b70_79Count: 0,
        failCount: 0,
        b60_69Count: 0,

      },
      averageScore: 0,
      highestScore: 0,
      lowestScore: 0,
    }
  }
  ,
  methods: {

    query() {
      if (this.c_id === '')
        this.$alert('查询编号不可为空哦！', '温馨提示', {
          confirmButtonText: '知道啦',
        })
      else {
        axios.get('/teachers/gradeDistribution?cid=' + this.c_id).then((res) => {
          console.log(res.data)
          if (res.data.flag) {

            this.list.b60_69Count = res.data.data.b60_69Count
            this.list.b70_79Count = res.data.data.b70_79Count
            this.list.b80_89Count = res.data.data.b80_89Count
            this.list.b90_100Count = res.data.data.b90_100Count
            this.list.failCount = res.data.data.failCount

            this.averageScore = res.data.data.averageScore
            this.highestScore = res.data.data.highestScore
            this.lowestScore = res.data.data.lowestScore

            this.$message.success('更新成功，清浏览右侧内容！')

          } else {
            this.$message.error(res.data.msg)
          }

        })
      }
    }
  },

  mounted() {
    axios.get("/teachers/myCourse").then((res)=>{
      console.log(res.data)
      this.options=res.data.data
    })
    var myChart = echarts.init(document.getElementById('main'));
    var option= {
        title: {
          text: '成绩分布柱状图'
        },
        tooltip: {},
        legend: {
          data: ['成绩分布']
        },
        xAxis: {
          data: ['90-100（A）','80-89(B)','70-79(C)','60-69(D)','60以下(E)']
        },
        yAxis: {},
        series: [
          {
            name: '成绩分布',
            type: 'bar',
            data: []
          }
        ]
      }
    myChart.setOption(option, true);

    setInterval(()=>{
          option.series[0].data=[this.list.b90_100Count,
            this.list.b80_89Count,
            this.list.b70_79Count,
            this.list.b60_69Count,
            this.list.failCount]
          myChart.setOption(option, true);
    }, 1000);
  }
}


</script>

<style scoped>
.center{
  width: 100%;
  display:flex;
  justify-content:space-around
}
</style>